import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import Alert from "react-bootstrap/Alert";
import axios from "axios";
import { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

const parseResult = (data = []) => {
  return data.map((candidato) => {
    return {
      name: candidato.nm,
      votos: candidato.vap,
      pvotos: candidato.pvap,
    };
  });
};

function App() {
  const [results, setResults] = useState([]);
  const [apurado, setApurado] = useState("0");
  const [isLoading, setLoading] = useState(false);

  const fetchData = () => {
    setLoading(true);
    axios
      .get(
        "https://resultados.tse.jus.br/oficial/ele2022/" +
          "544/dados-simplificados/br/br-c0001-e000544-r.json"
      )
      .then((response) => {
        if (response.status === 200) {
          setResults(parseResult(response.data.cand));
          setApurado(response.data.pst);
        }
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    const timer = setInterval(() => fetchData(), 5000);
    return () => clearTimeout(timer);
  }, []);

  const Loading = () => {
    return (
      isLoading && (
        <Alert key="warning" variant="warning">
          Refreshing..
        </Alert>
      )
    );
  };
  return (
    <div>
      <Loading />
      <Alert key="success" variant="success">
        Apuração {apurado} %
      </Alert>
      <button onClick={fetchData}>Refresh</button>
      <BootstrapTable data={results} striped={true} hover={true}>
        <TableHeaderColumn
          dataField="name"
          isKey={true}
          dataAlign="center"
          dataSort={true}
        >
          Nome
        </TableHeaderColumn>
        <TableHeaderColumn dataField="votos" dataSort={true}>
          Votos
        </TableHeaderColumn>
        <TableHeaderColumn dataField="pvotos">P. Votos</TableHeaderColumn>
      </BootstrapTable>
    </div>
  );
}

export default App;
